<template>
	<div style="background-color:#DEEEEE; min-height:850px">
		<el-row class="stockform-header" style="background-color:#A9A9BF;">
	      <el-col :span="24">
	        <div>
	            <h2 style="text-align:center;"></h2>
	        </div>
	      </el-col>
	    </el-row>

		<el-row class="stockform-header" style="background-color:#E9A9BF; margin-top:80px;">
	      <el-col :span="12" style="background-color:#C9A9BF">
	        <div>
	            <h3 style="text-align:center;"></h3>
	            <el-upload class="upload-demo" drag action="/api/upfileaddbom" style="margin: 0 210px;" :on-success="addsuccess">
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text"><em></em></div>
				  <div class="el-upload__text" style="color:red;">xlsx2M</div>
				  <div class="el-upload__text" style="color:red;">xlsx</div>
				</el-upload>
	        </div>
	      </el-col>
	      <el-col :span="12" style="background-color:#89A9BF">
	        <div>
	            <h3 style="text-align:center;">BOM</h3>
	            <el-upload class="upload-demo" drag action="/api/upfilecomparebom" style="margin: 0 210px;" :on-success="success_res">
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text"><em></em></div>
				  <div class="el-upload__text" style="color:red;">xlsx2M</div>
				  <div class="el-upload__text" style="color:red;">xlsx</div>
				</el-upload>
	        </div>
	      </el-col>
	    </el-row>
	    <el-row class="stockform-header" style="background-color:#E9A9BF;">
	      <el-col :span="12" style="background-color:#B9A9BF">
	        <div>
	            <h3 style="text-align:center;"></h3>
	            <el-upload class="upload-demo" drag action="/api/upfileaddbomnum" style="margin: 0 210px;" :on-success="addnumsuccess">
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text"><em></em></div>
				  <div class="el-upload__text" style="color:red;">xlsx2M</div>
				  <div class="el-upload__text" style="color:red;">xlsx</div>
				</el-upload>
	        </div>
	      </el-col>
	      <el-col :span="12" style="background-color:#99A9BF">
	        <div>
	            <h3 style="text-align:center;"></h3>
	            <el-upload class="upload-demo" drag action="/api/upfilesubbomnum" style="margin: 0 210px;" :on-success="subnumsuccess">
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text"><em></em></div>
				  <div class="el-upload__text" style="color:red;">xlsx2M</div>
				  <div class="el-upload__text" style="color:red;">xlsx</div>
				</el-upload>
	        </div>
	      </el-col>
	    </el-row>
	</div>
</template>

<script>
import xlsx from '@/utils/exportxlsx'

export default {
	methods: {
		success_res(res,file,filelist) {
			rec = xlsx.exportXls(res.data)
			if(rec == true)
		    {
		        this.$message({
		            type: 'success',
		            message: ''
		        });
		    }else
		    {
		        this.$message({
		        	type: 'warning',
		            message: ''
		        });
		    }
		},

		addsuccess(response,file,filelist){
		    if(response.success == true)
		    {
		        this.$message({
		            type: 'success',
		            message: response.message
		        });
		    }else
		    {
		        this.$message({
		        	type: 'warning',
		            message: 'BOM'
		        });
		    }
		},

		addnumsuccess(response,file,filelist){
		    if(response.success == true)
		    {
		        this.$message({
		            type: 'success',
		            message: response.message
		        });
		    }else
		    {
		        this.$message({
		        	type: 'warning',
		            message: 'BOM'
		        });
		    }
		},

		subnumsuccess(response,file,filelist){
		    if(response.success == true)
		    {
		        this.$message({
		            type: 'success',
		            message: response.message
		        });
		    }else
		    {
		        this.$message({
		        	type: 'warning',
		            message: 'BOM'
		        });
		    }
		},
	},
}
</script>


